.app-wrapper {
    display: grid;
    width: 1200px;
    margin: 0 auto;

    grid-template-areas:
            "h h"
            "n c";
    grid-template-rows: 60px 1fr;
    grid-template-columns:  15% 1fr;
    grid-gap: 10px;
}

.app-wrapper-contents {
    grid-area: c;
    background-color:white;
}










